import { createRouter, createWebHistory } from 'vue-router'
import LandingPageHome from '@/pages/LandingPageHome.vue';
import LandingPageAboutUs from '@/pages/LandingPageAboutUs.vue';

const router = createRouter({
  history : createWebHistory(),
  routes: [
    {
      path: '/',
      name: 'home',
      component: LandingPageHome
    },
    {
      path: '/about-us',
      name: 'about-us',
      component: LandingPageAboutUs
    }
  ]
});
export default router;
